


























/* only for app */
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#skill-generator {
  width: 700px
}
#output {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
/* common */
.skills-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.skills-master-order {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 5px;
}
.skills-master-order span {
  margin: 10px;
}
.skill-icon {
  width: 60px;
  height: 60px;
}
.skill-wrapper {
  border: 1px solid #c9c9c9
}
.skill-letter {
  text-align: center;
}
.skills-select-table {
  margin: 5px;
  border: 1px solid #c9c9c9;
  text-align: center;
  border-collapse: collapse
}
.skills-table-header {
  background: #fff;
  border: 1px solid #999;
  width: 25px;
  height: 25px;
}
.skills-table-cell {
  background: #f9f9f9;
  border: 1px solid #d9d9d9;
  width: 25px;
  height: 25px;
}
.skills-table-cell-active {
  background: rgb(59, 183, 255);
}









































































































